import Vue from 'vue'
import VueRouter from 'vue-router'
import Images from '../components/Images.vue'
import Login from '../components/Login.vue'


Vue.use(VueRouter)

const routes = [
  {
    path: '/login',
    name: 'Login',
    component: Login
  },
  {
    path: '/',
    name: 'Home',
    component: Images
  },
  {
    name: 'Images',
    path: '/images',
    component: () => import(/* webpackChunkName: "images" */'@/components/Images.vue')
  },
  {
    name: 'ImageUpload',
    path: '/images/upload',
    component: () => import(/* webpackChunkName: "image-upload" */'@/components/ImageUpload.vue')
  },
  {
    name: 'Image',
    path: '/images/:id',
    component: () => import(/* webpackChunkName: "image" */'@/components/Image.vue')
  },
]

const router = new VueRouter({
  mode: 'history',
  base: process.env.BASE_URL,
  routes
})

router.beforeEach((to, from, next) => {
  if (to.name == 'Login') {
    next()
    return
  }

  if (localStorage.jwt == '') next({ name: 'Login' })
  next()
})
  

export default router
