import axios from "axios";

axios.defaults.withCredentials = true

export default {
    create: images => axios.post(`${process.env.VUE_APP_BACKEND_ROOT}/images`, images),
    getAll: () => axios.get(`${process.env.VUE_APP_BACKEND_ROOT}/images`),
    getById: id => axios.get(`${process.env.VUE_APP_BACKEND_ROOT}/images/${id}`),
    login: formdata => axios.post(`${process.env.VUE_APP_BACKEND_ROOT}/login`, formdata)
  }