<template>
  <div>
      <v-container>
          <v-row class='justify-center'>
              <v-col cols=4>
                    <input v-model="username" placeholder="username">
                    <input v-model="password" placeholder="password">
                    <v-btn color="primary" @click="login()">Login</v-btn> 
              </v-col>
          </v-row>
      </v-container>
    
  </div>
</template>

<script>
import imagesApi from '@/api/images';

export default {
    data: () => {
        return {
            username: '',
            password: ''
        }
    },
    methods: {
        login() {

            let formData = new FormData();
            formData.set('username', this.username);
            formData.set('password', this.password);

            imagesApi.login(formData).then((response) => {
                console.log('Saving cookie: ' + response.headers['Set-Cookie'])
                localStorage.jwt = response.headers['Set-Cookie']
                    this.$router.push({path: '/images'}).catch(() => {});
                })
                .catch(error => {
                    console.log(error)
                    alert("Failed login");
                });
            }
  }
}
</script>

